<template>
  <div id="app">
    <div class="fixed">
      <nav>
        <router-link to="/"></router-link>
        <router-link to="/playlist"></router-link>
      </nav>
    </div>
    <router-view
      class="routerView"
      @playsong="playsongEl"
      @playlist="playlist = $event"
      @playid="playid = $event"
      @playlistitem="playsongEl"
      @hotitem="playsongEl"
      @searchplay="playsongEl"
      :playing="playing"
      :songId="songId"
      :style="{ marginBottom: playsong ? '50rem' : 0 }"
    />

    <audio
      :src="songSrc"
      ref="audio"
      autoplay
      @play="playing = true"
      @pause="playing = false"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
      @volumechange="volume = $event.target.volume"
    ></audio>

    <PlayBar
      :playsong="playsong"
      :playlist="playlist"
      :playid="playid"
      :view="view"
      @tage-view="tageview"
      :playing="playing"
      @open="open = $event"
      @song-play="$refs.audio.play()"
      @song-pause="$refs.audio.pause()"
      @barsong="playsongEl"
      :duration="duration"
      :currentTime="currentTime"
    />

    <PlayPage
      :playsong="playsong"
      :playlist="playlist"
      :playid="playid"
      :views="view"
      @tage-view="tageview"
      @tage-song="tagesong"
      @tage-model="tagemodel"
      :model="model"
      :playing="playing"
      :lyric="lyric"
      :open="open"
      @close="open = $event"
      @page-play="$refs.audio.play()"
      @page-pause="$refs.audio.pause()"
      @pagesong="playsongEl"
      :duration="duration"
      :currentTime="currentTime"
      @change-currentTime="$refs.audio.currentTime = $event"
      @volume="$refs.audio.volume = $event"
      :volume="volume"
    />
    <!-- @volume="$event.target.audioHuds = $event" -->
    <ListMini
      :playlist="playlist"
      :playid="playid"
      :view="view"
      :playing="playing"
      :songId="songId"
      @tage-view="tageview"
      @playsong="playsongEl"
      @deletesong="deletesongEl"
    />
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayPage from "@/components/PlayPage.vue";
import ListMini from "@/components/ListMini.vue";
export default {
  components: {
    PlayBar,
    PlayPage,
    ListMini,
  },

  name: "HomeView",

  data: function () {
    return {
      playsong: null,
      playlist: [],
      playid: "",
      view: 1,
      models: ["顺序", "随机", "单曲"],
      model: "顺序",
      playing: false,
      open: null,
      duration: 0,
      currentTime: 0,
      volume:0.7,
      lyric: null,
      songSrc: null,
      songId: null,
    };
  },

  methods: {
    playsongEl: function (song) {
      console.log(song);
      (this.playsong = song),
        (this.songId = song.id),
        (this.songSrc =
          "https://music.163.com/song/media/outer/url?id=" +
          this.playsong.id +
          ".mp3");

      //歌词
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: { id: song.id },
        })
        .then((res) => {
          this.lyric = res.data.lrc.lyric
            .split("\n")
            .filter((o) => o)
            .map((o) => {
              var { groups } = o.match(
                /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
              );
              return {
                time: groups.mm * 60 + groups.ss * 1,
                text: groups.text.trim(),
              };
            });
        });
        this.$refs.audio.currentTime = 0;
    },

    tageview: function (e) {
      this.view = this.view + e;
    },

    tagesong: function (tage) {
      switch (this.model) {
        case "顺序":
          var currentSongIndex = this.playlist.findIndex(
            (o) => o.id === this.playsong.id
          );
          if (tage === "next") {
            this.playsong =
              currentSongIndex === this.playlist.length - 1
                ? this.playlist[0]
                : this.playlist[currentSongIndex + 1];
          } else {
            this.playsong =
              currentSongIndex === 0
                ? this.playlist[this.playlist.length - 1]
                : this.playlist[currentSongIndex - 1];
          }
          break;

        case "随机":
          var daixuan = this.playlist.filter((o) => o.id !== this.playsong.id);
          this.playsong = daixuan[Math.floor(Math.random() * daixuan.length)];
          break;

        case "单曲":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
      }
      this.songId = this.playsong.id,
      this.$refs.audio.currentTime = 0;

      // 当切换歌曲时, 更新(监听)一下,当前歌曲的id
      this.songSrc =
        "https://music.163.com/song/media/outer/url?id=" +
        this.playsong.id +
        ".mp3";

      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: { id: this.playsong.id },
        })
        .then((res) => {
          this.lyric = res.data.lrc.lyric
            .split("\n")
            .filter((o) => o)
            .map((o) => {
              var { groups } = o.match(
                /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
              );
              return {
                time: groups.mm * 60 + groups.ss * 1,
                text: groups.text.trim(),
              };
            });
        });

    },

    tagemodel: function () {
      var index = this.models.findIndex((o) => o === this.model);
      this.model =
        index === this.models.length - 1
          ? this.models[0]
          : this.models[index + 1];
    },

    deletesongEl: function (id) {
      console.log(id);
      this.playlist =  this.playlist.filter((o) => o.id !== id );
    },
  },

  watch: {
    currentTime: function (k) {
      if (k == this.$refs.audio.duration) {
        this.tagesong("next");
      }
    },
  },
};
</script>



