<template>
  <div class="view3" v-if="results && !focus">
    <li
      class="search-item"
      v-for="(result, index) in results"
      :key="index"
      @click="
        $emit('playsong', result);
        $emit('playlist', results);
        $emit('playid', 'searchsong');
      "
    >
      <div class="left">
        <p>{{ result.name }}</p>
        <span class="SQ" v-if="result.artists.length == 1"></span>
        <span>{{ result.artists[0].name }} {{ result.album.name }}</span>
      </div>
      <div class="play" v-if="result.id !== songId"></div>
      <div v-else class="play active" :class="{ playing: playing }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </li>
  </div>
</template>

<script>
export default {
  props: {
    results: Array,
    isView: String,
    focus: Boolean,
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
.view3 {
  li.search-item {
    width: 100%;
    box-shadow: 0px 0rem 1rem 0px rgb(208, 208, 208);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10rem;
    // background: rgb(100, 41, 41);
    .left {
      width: 90%;
      p {
        font-size: 17rem;
      }
      .SQ {
        width: 20px;
        height: 10px;
        background-image: url(../assets/sprit.png);
        display: inline-block;
        background-size: 1040%;
      }
      span {
        font-size: 2rem;
        color: #888888;
      }
    }

    .play {
      width: 20rem;
      height: 20rem;
      position: relative;
      top: 0;
      left: -5rem;
      background-image: url(../assets/sprit.png);
      background-repeat: no-repeat;
      background-position: -22rem 0rem;
      // background-color: teal;
      background-size: 150rem;
      @keyframes playingrunning {
        from {
          transform: rotateX(15deg);
        }
        to {
          transform: rotateX(60deg);
        }
      }
      &.active {
        background-image: none;
        display: flex;
        justify-content: space-around;
        span {
          width: 10%;
          height: 100%;
          // background: red;
          background: #34a95b;
          animation: playingrunning 0.8s infinite alternate;
          animation-play-state: paused;
          transform-origin: bottom;
          border-radius: 50rem;
          &:nth-child(1) {
            animation-delay: 0s;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
          }
          &:nth-child(3) {
            animation-delay: -0.4s;
          }
          &:nth-child(4) {
            animation-delay: -0.6s;
          }
        }

        &.playing {
          span {
            animation-play-state: running;
          }
        }
      }
    }

    .active {
      display: none;
    }
  }
}
</style>