<template>
  <div>
    <div class="playbar" @click.stop="$emit('open', open)" v-if="playsong">
      <img
        v-if="playid == 'newsong'"
        :class="{ playing: playing }"
        :src="`${playsong.picUrl}?imageView=1&type=webp&thumbnail=80x0`"
      />
      <img
        v-if="playid == 'cardsong'"
        :class="{ playing: playing }"
        :src="`${playsong.al.picUrl}?imageView=1&type=webp&thumbnail=80x0`"
      />
      <img
        v-if="playid == 'hotsong'"
        :class="{ playing: playing }"
        :src="`${playsong.al.picUrl}?imageView=1&type=webp&thumbnail=80x0`"
      />
      <img
        v-if="playid == 'searchsong'"
        :class="{ playing: playing }"
        :src="`${playsong.artists[0].img1v1Url}?imageView=1&type=webp&thumbnail=80x0`"
      />

      <div class="text">
        <div v-if="playid == 'newsong'">
          <h3>{{ playsong.name }}</h3>
          <p>
            {{ playsong.song.album.artists[0].name }} -
            {{ playsong.song.album.name }}
          </p>
        </div>

        <div v-if="playid == 'cardsong'">
          <h3>{{ playsong.name }}</h3>
          <p>
            {{ playsong.ar[0].name }} -
            {{ playsong.al.name }}
          </p>
        </div>
        <div v-if="playid == 'hotsong'">
          <h3>{{ playsong.name }}</h3>
          <p>
            {{ playsong.ar[0].name }} -
            {{ playsong.al.name }}
          </p>
        </div>
        <div v-if="playid == 'searchsong'">
          <h3>{{ playsong.name }}</h3>
          <p>
            {{ playsong.artists[0].name }} -
            {{ playsong.album.name  }}
          </p>
        </div>
      </div>
      <div class="playbtn">
        <i
          class="fa fa-play"
          @click.stop="$emit('song-play')"
          v-if="!playing"
        ></i>
        <i class="fa fa-pause" @click.stop="$emit('song-pause')" v-else></i>
        <canvas ref="canvas" width="30" height="30"></canvas>
      </div>

      <div class="list fa fa-bars" @click.stop="$emit('tage-view', 1)" :class="{active:view%2 == 0 }"></div>
    </div>
  </div>
</template>

<script>
import ListMini from "@/components/ListMini.vue";
export default {
  components: {
    ListMini,
  },

  props: {
    playsong: Object,
    playlist: Array,
    playid: String,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    view:Number,
  },
  data: function () {
    return {
      open: true,
    };
  },
  methods: {},

  computed: {
    persontage: function () {
      return this.currentTime / this.duration;
    },
  },

  watch: {
    persontage: function (n) {
      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");
      context.strokeStyle="rgb(255, 72, 40)";
      context.lineWidth = 5;
      context.clearRect(0, 0, 30, 30);
      context.beginPath();
      context.arc(15, 15, 16, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      context.stroke();
    },
  },
};
</script>

