            <p>{{ (playlists?.playCount / 10000).toFixed(1) }}万</p>
            <span>歌单</span>
          </div>
          <div class="test">
            <h3>{{ playlists?.name }}</h3>
            <div class="singer">
              <div class="singerphoto">
                <img :src="playlists?.creator.avatarUrl" alt="" />
                <img
                  class="icon"
                  :src="playlists?.creator.avatarDetail?.identityIconUrl"
                  alt=""
                />
              </div>
              <span>{{ playlists.creator.nickname }}</span>
            </div>
          </div>
          <div class="reback fa fa-chevron-down" @click="$router.back()"></div>
        </div>
        <div class="mask"></div>
      </div>
      <PlayListItem
        @playsong="$emit('playsong', $event)"
        @playlist="$emit('playlist', $event)"
        @playid="$emit('playid', $event)"
        :playlist="playlists.tracks"
        :playing="playing"
        :songId="songId"
      />
      <div class="jc">
        <h4>精彩评论</h4>
      </div>
      <PlayComments :commentslist="commentslist" />
      <div class="collect">
