</template>

<script>
import PlayListItem from "@/components/PlayListItem.vue";
import PlayComments from "@/components/PlayComments.vue";
export default {
  components: {
    PlayListItem,
    PlayComments,
  },
  props: {
    songId: [Number, String],
    playing: Boolean,
  },
  data: function () {
    return {
      playlists: null,
      commentslist: [],
    };
  },

  methods: {
    getPlayList: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        })
        .then((res) => {
          this.playlists = res.data.playlist;
        });
    },
    getComments: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: { id },
        })
        .then((res) => {
          console.log(res.data);
          this.commentslist = res.data.hotComments;
        });
    },
  },

  // 当创建之后, 将 this.$route.query.id 作为实参传给 --> getPlayList
  created: function () {
    console.log(this.$route.query.id);
    this.getPlayList(this.$route.query.id);
    this.getComments(this.$route.query.id);
  },
};
</script>

