      @searchBtn="searchBtn"
      @searchHistory="searchHistory"
    />
    <SviewTwo
      :suggests="suggests"
      :value="value"
      :focus="focus"
      :loading="loading"
      @ClickList="ClickList"
    />
    <SviewThree
      @playsong="$emit('playsong', $event)"
      @playlist="$emit('playlist', $event)"
      @playid="$emit('playid', $event)"
      :results="results"
      :focus="focus"
      @searchplay="$emit('searchplay', $event)"
      :songId="songId"
      :playing="playing"
    />
  </div>
