<script>
import SviewOne from "@/components/SviewOne.vue";
import SviewTwo from "@/components/SviewTwo.vue";
import SviewThree from "@/components/SviewThree.vue";

import { searchHot } from "@/apis";

export default {
  components: {
    SviewOne,
    SviewTwo,
    SviewThree,
  },

  props: {
    songId: [Number, String],
    playing: Boolean,
  },

  data: function () {
    return {
      value: "",
      btns: [],
      suggests: [],
      results: [],
      focus: false,
      history: JSON.parse(localStorage.getItem("history")) || [],
      loading: false,
    };
  },

  methods: {
    // 搜索中的关键词
    SearchSuggests: function (keywords) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: { keywords, type: "mobile" },
        })
        .then((res) => {
          this.loading = false;
          console.log("搜索中", res.data.result.allMatch);
          this.value
            ? (this.suggests = res.data.result.allMatch)
            : (this.suggests = []);
        });
    },

    // 搜索的结果
    SearchResult: function (keywords) {
      this.loading = true;
      if (keywords) {
        this.axios
          .get("https://apis.netstart.cn/music/search", {
            params: { keywords, limit: 10, offset: 0 },
          })
          .then((res) => {
            this.loading = false;
            console.log("搜索结果:", res.data.result.songs);
            this.results = res.data.result.songs;
          });
      }
      this.history = [...new Set([keywords, ...this.history])];
    },

    // 删除历史记录
    deletehistory: function (h) {
      this.history = this.history.filter((o) => o !== h);
    },

    searchBtn: function (keyword) {
      console.log(keyword);
      this.value = keyword;
      this.SearchResult(keyword);
    },

    searchHistory: function (keyword) {
      console.log(keyword);
      this.value = keyword;
      this.SearchResult(keyword);
    },

    ClickList: function (keyword) {
      console.log(keyword);
      this.value = keyword;
      this.SearchResult(keyword);
      this.focus = false;
    },
  },

  // 初始页面的 热搜
  // 方法一: 将链接单独放进一个文件, 经过路由传入
  created() {
    this.loading = true;
    searchHot().then((res) => {
      console.log(res.data.result);
      this.btns = res.data.result.hots;
      this.loading = false;
    });
  },

  // 方法二:
  // created() {
  //   this.loading = true;
  //   axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
  //     console.log(res.data.result);
  //     this.btns = res.data.result.hots;
  //     this.loading = false;
  //   });
  // },

  watch: {
    value: function (v) {
      if (v) {
        this.SearchSuggests(v);
      } else {
        this.results = [];
      }
    },

    history: function (localHistory) {
      localStorage.setItem("history", JSON.stringify(localHistory));
    },
  },
};
</script>

<style lang="less" scoped>
