    height: 25rem;
    border-radius: 0 50rem 50rem 0;
    border: none;
    background: #ebecec;
    outline: none;
  }
  span {
    width: 15rem;
    height: 15rem;
    background: #bcbdbd;
    text-align: center;
    line-height: 15rem;
    border-radius: 50%;
    font-size: 2rem;
    transform: rotate(45deg);
