<template>
  <ul class="newlist">
    <li
      v-for="newsong in newsongs"
      :key="newsong.id"
      class="song-item"
      @click="
      $emit('playsong', newsong);
      $emit('playlist', newsongs);
      $emit('playid', 'newsong');
      "
    >
      <!-- @click="$emit('playsong', {allsong: newsongs, clicksong:newsong, id: newsong.id})" -->
      <div class="left">
        <p>{{ newsong.name }}</p>
        <div class="dis">
          <span class="SQ" v-if="newsong.song.sqMusic !== null"></span>
          <span
            >{{ newsong.song.album.artists[0].name }} -
            {{ newsong.song.album.name }}</span
          >
        </div>
      </div>
      <div class="play" v-if="newsong.id !== songId"></div>
      <div v-else class="play active" :class="{ playing: playing }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    newsongs: Array,
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
li.song-item {
  width: 100%;
  // box-shadow: 0px 0rem 1rem 0px rgb(208, 208, 208);
  border-bottom: 0.5rem solid rgb(231, 231, 231);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10rem;
  .left {
    width: 90%;
    p {
      width: 300rem;
      height: 21rem;
      font-size: 17rem;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      // background: tomato;
    }
    .dis {
      display: flex;
      align-items: center;
    }
    .SQ {
      width: 20px;
      height: 10px;
      background-image: url(../assets/sprit.png);
      display: inline-block;
      background-size: 1040%;
    }
    span {
      width: 300rem;
      display: inline-block;
      font-size: 2rem;
      color: #888888;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  .play {
    width: 20rem;
    height: 20rem;
    background-image: url(../assets/sprit.png);
    background-repeat: no-repeat;
    background-position: -22rem 0rem;
    // background-color: teal;
    background-size: 150rem;
    @keyframes playingrunning {
      from {
        transform: rotateX(15deg);
      }
      to {
        transform: rotateX(60deg);
      }
    }
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 10%;
        height: 100%;
        background: #34a95b;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        border-radius: 50rem;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>