<template>
  <div class="hot">
    <ul v-for="(hotsong, index) in hotsongs" :key="index">
      <li
        v-if="index < 10 * add"
        class="hot-item"
        @click="
          $emit('playsong', hotsong);
          $emit('playlist', hotsongs);
          $emit('playid', 'hotsong');
        "
      >
        <div class="num">{{ index >= 9 ? index + 1 : "0" + (index + 1) }}</div>
        <div class="songname">
          <p>{{ hotsong.name }}</p>
          <span class="SQ" v-if="hotsongs.sq !== null"></span>
          <span class="text"
            >{{ hotsong.ar[0].name }}-{{ hotsong.al.name }}</span
          >
        </div>
        <div class="play" v-if="hotsong.id !== songId"></div>
        <div v-else class="play active" :class="{ playing: playing }">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    hotsongs: Array,
    songId: [Number, String],
    playing: Boolean,
    add: Number,
  },
};
</script>

<style lang="less" scoped>
.hot-item {
  width: 100%;
  box-shadow: 0px 0rem 1rem 0px rgb(208, 208, 208);
  display: flex;
  align-items: center;
  padding: 10rem;
  .num {
    width: 30rem;
    // color: rgb(232, 22, 22);
    color: #34a95b;
    font-size: 18rem;
  }
  .songname {
    width: 90%;
    p {
      font-size: 17rem;
    }
    .SQ {
      width: 20rem;
      height: 12rem;
      background-image: url(../assets/sprit.png);
      display: inline-block;
      background-size: 1040%;
      // background-color: rgb(3, 189, 214);
    }
    .text {
      width: 270rem;
      font-size: 2rem;
      color: #888888;
      display: inline-block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  .play {
    width: 20rem;
    height: 20rem;
    position: relative;
    top: 0;
    left: -5rem;
    background-image: url(../assets/sprit.png);
    background-repeat: no-repeat;
    background-position: -22rem 0rem;
    // background-color: teal;
    background-size: 150rem;
    @keyframes playingrunning {
      from {
        transform: rotateX(15deg);
      }
      to {
        transform: rotateX(60deg);
      }
    }
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 10%;
        height: 100%;
        // background: red;
        background: #34a95b;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        border-radius: 50rem;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
.hot h3 {
  text-align: center;
  padding: 15rem 0;
  color: #999999;
  font-size: 14rem;
}
</style>