            />
            <img
              v-if="playid == 'searchsong'"
              :src="`${playsong.artists[0].img1v1Url}?imageView=1&type=webp&thumbnail=150x0`"
            />
          </div>
        </div>

        <div class="lyrics" v-else>
          <div class="song-lyrics" @click="flip++">
            <div
              class="lyricbg"
              :style="{
                transform: `translateY( ${
                  [...lyric].findIndex((o) => o.time >= currentTime) * -25 + 150
                }rem)`,
              }"
            >
              <ul v-for="(ly, index) in lyric" :key="index">
                <li
                  :style="
                    [...lyric].reverse().length -
                      1 -
                      [...lyric]
                        .reverse()
                        .findIndex((o) => o.time < currentTime) ===
                    index
                      ? { color: '#202d39', fontSize: '16rem' }
                      : { color: '', font_size: ''}
                  "
                >
                  {{ ly.text }}
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="control">
          <div class="progress">
            <div class="nowtime">
              {{
                parseInt(currentTime / 60) > 9
                  ? parseInt(currentTime / 60)
                  : "0" + parseInt(currentTime / 60)
              }}:{{
                parseInt(currentTime % 60) > 9
                  ? parseInt(currentTime % 60)
                  : "0" + parseInt(currentTime % 60)
              }}
            </div>
            <div class="zzz">
              <p>
                <i
                  :style="{ width: (currentTime * 250) / duration + 'rem' }"
                ></i>
                <span
                  :style="{ left: (currentTime * 250) / duration - 5 + 'rem' }"
                  v-if="timeicon"
                ></span>
              </p>
              <input
                class="musicprogress"
                type="range"
                step="0.01"
                min="0"
                :max="`${duration}`"
                :value="dragingTime"
                @input="
                  $emit(
                    'change-currentTime',
                    $event.target.value,
                    (timeicon = true)
                  )
                "
              />
            </div>
            <div class="alltime">
              {{
                parseInt(duration / 60) > 9
                  ? parseInt(duration / 60)
                  : "0" + parseInt(duration / 60)
              }}:{{
                parseInt(duration % 60) > 9
                  ? parseInt(duration % 60)
                  : "0" + parseInt(duration % 60)
              }}
