                class="fa fa-play next"
                @click="$emit('tage-song', 'next')"
              ></i>
            </div>

            <div
              class="list fa fa-bars"
              @click.stop="$emit('tage-view', 1)"
              :class="{ active: views % 2 == 0 }"
            ></div>
          </div>
