        <div
          v-if="playid == 'newsong'"
          class="mask1"
          :style="{
            backgroundImage: `url(${playsong.picUrl}?imageView=1&type=webp&thumbnail=120x0)`,
          }"
        ></div>
        <div
          v-if="playid == 'cardsong'"
          class="mask1"
          :style="{
            backgroundImage: `url(${playsong.al.picUrl}?imageView=1&type=webp&thumbnail=120x0)`,
