          v-if="playid == 'searchsong'"
          class="mask1"
          :style="{
            backgroundImage: `url(${playsong.artists[0].img1v1Url}?imageView=1&type=webp&thumbnail=120x0)`,
          }"
        ></div>
        <div class="mask"></div>
      </div>
    </div>
  </div>
</template>

<script>
import ListMini from "@/components/ListMini.vue";
export default {
  components: {
    ListMini,
  },

  props: {
    playsong: Object,
    playlist: Array,
    playid: String,
    model: String,
    open: Boolean,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    lyric: Array,
    volume: Number,
    views: Number,
  },
  data: function () {
    return {
      flip: 0,
      dragingTime: null,
      show: false,
      volumenum: false,
      timeicon: false,
    };
  },

  watch: {
    currentTime: function () {
      this.dragingTime = this.currentTime;
    },
    show: function () {
      setTimeout(() => (this.show = false), 1000);
    },
    volumenum: function () {
      setTimeout(() => (this.volumenum = false), 2000);
    },
    timeicon: function () {
      setTimeout(() => (this.timeicon = false), 2000);
    },
  },
};
</script>

<style lang="less" scoped>
.playpage {
  width: 375rem;
  height: 100vh;
  position: relative;
  z-index: 9999;
  color: #fff;
  .playing {
    width: 375rem;
    height: 100vh;
    // background: rgb(22, 133, 164);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: fixed;
    top: 0;
    // font-size: 18rem;
    left: 0;
    z-index: 9;
    display: flex;
    flex-direction: column;
    align-items: center;
    .header {
      width: 375rem;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 5rem;
      padding: 10rem 0;
      .reback {
        // background: tomato;
        padding: 5rem 8rem;
        position: relative;
        top: 0;
        left: 10rem;
        font-size: 16rem;
        font-weight: lighter;
      }
      .title {
        width: 200rem;
        // background: rgb(108, 108, 108);
        text-align: center;
        margin: 0 auto;
        overflow: hidden;

        h3 {
          width: 200rem;
          font-size: 17rem;
          font-weight: bolder;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
        p {
          width: 200rem;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          margin-top: 3rem;
          letter-spacing: 1rem;
          color: rgb(208, 208, 208);
        }
      }
      .menu {
        // background: tomato;
        padding: 5rem 8rem 5rem 0;
        margin-right: 5rem;
        font-size: large;
        position: relative;
        top: 0;
        left: -15rem;
      }
    }
    .volume {
      position: relative;
      span {
        position: absolute;
        top: 1rem;
        left: -20rem;
        font-size: 16rem;
      }
      p {
        width: 180rem;
        height: 3rem;
        background: rgb(241, 241, 241);
        position: absolute;
        top: 8rem;
        left: 0;
        border-radius: 10rem;
        i {
          height: 3rem;
          // background: rgb(234, 25, 25);
          background: #34a95b;
          display: inline-block;
          position: absolute;
          top: 0;
          left: 0;
        }
        span {
          width: 10rem;
          height: 10rem;
          background: tomato;
          background: #34a95b;
          border-radius: 50%;
          display: inline-block;
          z-index: 9;
          position: absolute;
          top: -3.5rem;
          left: 0rem;
          pointer-events: none;
        }
      }
      .volumeNum {
        position: absolute;
        top: 2rem;
        right: -35rem;
      }
      .volumess {
        width: 180rem;
        height: 3rem;
        opacity: 0;
      }
    }
    .css3 {
      position: relative;
      width: 375rem;
      height: 100%;
      // background: rgb(255, 0, 0);
      .needle {
        width: 85rem;
        height: 170rem;
        background-image: url(../assets/round-needle.png);
        background-repeat: no-repeat;
        background-size: 100%;
        position: absolute;
        top: 25rem;
        left: 50%;
        transform-origin: 12.5rem 12.5rem;
        margin-left: -10rem;
        z-index: 999;
        transform: rotate(-25deg);
        // transition: all 0.0.8s;
        &.take {
          transform: rotate(0deg);
        }
      }

      @keyframes playing {
        from {
          transform: rotate(0deg);
