<template>
  <div class="songlist">
    <ul v-for="(song, index) in playlist" :key="index">
      <li
        class="play-item"
        v-if="index < 10 * num"
        @click="
          $emit('playsong', song);
          $emit('playlist', playlist);
          $emit('playid', 'cardsong');
        "
      >
        <div class="num">{{ index >= 9 ? index + 1 : "0" + (index + 1) }}</div>
        <div class="songname">
          <p>{{ song.name }}</p>
          <span class="SQ"></span>
          <span>{{ song.ar[0].name }}-{{ song.al.name }}</span>
        </div>
        <div class="play" v-if="song.id !== songId"></div>
        <div v-else class="play active" :class="{ playing: playing }">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </li>
    </ul>
    <h4 @click="num++" v-if="num < 2">加载更多...</h4>
    <h4 v-else>已经加载到底啦~</h4>
  </div>
</template>

<script>
export default {
  props: {
    playlist: Array,
    songId: [Number, String],
    playing: Boolean,
  },

  data: function () {
    return {
      num: 1,
    };
  },
};
</script>

<style lang="less" scoped>
.songlist {
  ul {
    .play-item {
      width: 100%;
      box-shadow: 0px 0rem 1rem 0px rgb(208, 208, 208);
      display: flex;
      align-items: center;
      padding: 10rem;
      .num {
        width: 30rem;
        // color: #999999;
        color: #34a95b;
        font-size: 18rem;
      }
      .songname {
        width: 90%;
        p {
          width: 280rem;
          font-size: 17rem;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
        .SQ {
          width: 20px;
          height: 10px;
          background-image: url(../assets/sprit.png);
          display: inline-block;
          background-size: 1040%;
        }
        span {
          width: 250rem;
          color: #888888;
          display: inline-block;
          font-size: 2rem;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
      }

      .play {
        width: 20rem;
        height: 20rem;
        position: relative;
        top: 0;
        left: -5rem;
        background-image: url(../assets/sprit.png);
        background-repeat: no-repeat;
        background-position: -22rem 0rem;
        background-size: 150rem;

        @keyframes playingrunning {
          from {
            transform: rotateX(15deg);
          }
          to {
            transform: rotateX(60deg);
          }
        }
        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;
          span {
            width: 10%;
            height: 100%;
            // background: red;
            background: #34a95b;
            animation: playingrunning 0.8s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;
            border-radius: 50rem;
            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }

          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }
  h4 {
    text-align: center;
    padding: 15rem 0;
    font-size: 14rem;
  }
}
</style>