<script>
import HotItem from "@/components/HotItem.vue";

export default {
  components: {
    HotItem,
  },

  props: {
    songId: [Number, String],
    playing: Boolean,
  },

  data: function () {
    return {
      hotsongs: [],
      month: new Date().getUTCMonth() + 1,
      date: new Date().getUTCDate(),
      loading: false,
      add: 1,
    };
  },

  created() {
    (this.loading = true),
      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/personalized", {
          params: { limit: 6 },
        }),
        // this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
        this.axios.get(
          "https://apis.netstart.cn/music/playlist/detail?id=3778678"
        ),
      ]).then(([p, h]) => {
        (this.loading = false),
          (this.hotsongs = h.data.playlist.tracks.slice(0, 40));
      });
  },
};
</script>

