<template>
  <div>
    <div class="list" v-if="view % 2 == 0">
      <div class="minilist">
        <div class="title">
          <h3>当前菜单</h3>
          <span>( {{ playlist.length }} )</span>
        </div>

        <ul>
          <li
            v-for="(songs, i) in playlist"
            :key="i"
            @click.stop="$emit('playsong', songs)"
          >
            <div
              v-if="songs.id == songId"
              class="play active"
              :class="{ playing: playing }"
            >
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
            <p v-if="playid == 'newsong'">
              {{ songs.name }}-
              <span>{{ songs.song.album.artists[0].name }}</span>
            </p>
            <p v-if="playid == 'cardsong'">
              {{ songs.name }}-
              <span>{{ songs.ar[0].name }}</span>
            </p>
            <p v-if="playid == 'hotsong'">
              {{ songs.name }}-
              <span>{{ songs.ar[0].name }}</span>
            </p>
            <p v-if="playid == 'searchsong'">
              {{ songs.name }}-
              <span>{{ songs.artists[0].name }}</span>
            </p>
            <div class="icon" @click.stop="$emit('deletesong', songs.id)">+</div>
          </li>
        </ul>
      </div>
      <div class="mask" @click.stop="$emit('tage-view', 1)"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    playsong: Object,
    playlist: Array,
    playid: String,
    view: Number,
    playing: Boolean,
    songId: [Number, String],
  },
  data: function () {
    return {};
  },
};
</script>

<style lang="less" scoped>
.list {
  width: 20rem;
  font-size: 19rem;
  margin-right: 15rem;
  z-index: 99999;
  ul::-webkit-scrollbar {
    display: none;
  }
  .minilist {
    width: 300rem;
    // height: 380rem;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -170rem -150rem;
    box-shadow: 3rem 3rem 3rem 0 rgba(124, 124, 124, 0.374);
    border-radius: 16rem;
    box-sizing: content-box;
    z-index: 99999;
    .title {
      padding: 11rem 0;
      background: rgb(98, 98, 98);
      background: #34a95b;
      display: flex;
      align-items: baseline;
      border-radius: 16rem 16rem 0 0;
      h3 {
        margin: 0 10rem;
        color: #fff;
      }
      span {
        font-size: 13rem;
        color: rgba(255, 255, 255, 0.651);
      }
    }

    ul {
      width: 299rem;
      height: 350rem;
      padding-top: 3rem;
      background: #fafafa;
      overflow-x: hidden;
      overflow-y: scroll;
      border-radius: 0 0 16rem 16rem;
      li {
        height: 35rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1rem solid rgb(208, 208, 208);
        border-bottom: 1rem solid #34a95b58;
        margin: 0 10rem;
        .play {
          width: 20rem;
          height: 15rem;
          // background: tomato;
          margin-right: 5rem;
          @keyframes playingrunning {
            from {
              transform: rotateX(15deg);
            }
            to {
              transform: rotateX(60deg);
            }
          }
          &.active {
            background-image: none;
            display: flex;
            justify-content: space-around;
            span {
              width: 9%;
              height: 100%;
              background: #34a95b;
              animation: playingrunning 0.8s infinite alternate;
              animation-play-state: paused;
              transform-origin: bottom;
              border-radius: 50rem;
              &:nth-child(1) {
                animation-delay: 0s;
              }
              &:nth-child(2) {
                animation-delay: -0.2s;
              }
              &:nth-child(3) {
                animation-delay: -0.4s;
              }
              &:nth-child(4) {
                animation-delay: -0.6s;
              }
            }

            &.playing {
              span {
                animation-play-state: running;
              }
            }
          }
        }
        p {
          width: 250rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 15rem;
          span {
            font-size: 11rem;
            color: rgb(156, 156, 156);
          }
        }
        .icon {
          width: 20rem;
          height: 20rem;
          display: inline-block;
          border-radius: 50%;
          background: rgba(99, 99, 99, 0.35);
          text-align: center;
          line-height: 20rem;
          color: rgb(255, 255, 255);
          transform: rotate(45deg);
        }
      }
    }
  }
  .mask {
    width: 375rem;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
  }
}
</style>